async function bindleftCommonSearchdropdown(instance,modulename, filtername) {
    var vm = instance;
    let params = {};
    params["moduleName"] = modulename; //"CRM";
    params["type"] = filtername
   await DealDataService.GetLeftSearchStatus(params)
        .then(function (response) {
            var json = response.data;
            
            if (json != null) {
                if (filtername == "LEAD_DEAL_STATUS") {
                    vm.leftSearchFields[3].listOptions = json.DATA;
                    vm.StatusListForListing = json.DATA;
                    return vm.StatusListForListing;
                }
                if (filtername == "DEAL_OWNER_SEARCH") {
                    vm.leftSearchFields[1].listOptions = json.DATA;
                    vm.DealOwnerList = json.DATA
                    return vm.DealOwnerList;
                }
                if (filtername == "CRM_STAGE") {
                    vm.leftSearchFields[2].listOptions = json.DATA;
                    vm.DealStageList = json.DATA
                    return vm.DealStageList;
                }
            }
        });
}
export default { buttons, leftsearchSchema,listActions, bindleftCommonSearchdropdown };